import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ChatsAddComponent } from '../component-rooms/chats-add/chats-add.component';
import { ChatsViewComponent } from './chats-view/chats-view.component';

const routes: Routes = [
  {
    path: '',
    component: ChatsViewComponent
  },
  {
    path: 'add',
    component: ChatsAddComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ComponentChatsRoutingModule { }
